import { Component, OnInit } from '@angular/core';
import { FrontendService }    from '../frontend/frontend.service';

@Component({
  selector: 'app-ins-sheets',
  templateUrl: './ins-sheets.component.html',
  styleUrls: ['./ins-sheets.component.css'],
  providers: [FrontendService]
})

export class InsSheetsComponent implements OnInit {
  sheets = [];

  constructor(private _front: FrontendService)
  {

  }

  ngOnInit()
  {
    this.getFaq();
  }

  jsonparse(val)
  {
    val = JSON.parse(val);
    return val;
  }

  getFaq()
  {
    this._front.getSettings().subscribe(
      data => {
        if(data.success)
        {
          for (let variable of data.data)
          {
            if(variable['setName'] == 'InstructionSheet')
            {
              variable['setValue'] = JSON.parse(variable['setValue']);
              this.sheets.push(variable);
              // console.log(this.sheets);
            }
          }
          // this.faq = data.data['setValue'];
        }
      },
      err => console.log(err)
    );
  }

}



