import { Component, OnInit , Input , EventEmitter , Output} from '@angular/core';
import { ValidationErrors , AbstractControl , FormArray , FormControl , FormBuilder, FormGroup , Validators } from '@angular/forms';
declare var jQuery: any;
import { SuperadminService }    from '../../superadmin.service';
import { ToasterModule, ToasterService , Toast} from 'angular2-toaster';
import { Router, ActivatedRoute, Params } from '@angular/router';
function emailOrEmpty(control: AbstractControl): ValidationErrors | null {
    return control.value === '' ? null : Validators.email(control);
}
@Component({
  selector: 'app-edit-region',
  templateUrl: './edit-region.component.html',
  styleUrls: ['./edit-region.component.css'],
  providers: [SuperadminService]
})
export class EditRegionComponent implements OnInit {
  @Input() regionToEdit: string;
  private toasterService: ToasterService;
  regionUpdateTrue : boolean = false;
  updateRegion : FormGroup;
  regionDetails:any = {};
  @Output() onSuccess: EventEmitter<any> = new EventEmitter<any>();
  isCredentials : boolean = false;
  regionDetailsLoading : boolean = true;
  regionClass = 1;

  constructor( private router: Router , private _sp: SuperadminService , toasterService: ToasterService , private fb: FormBuilder)
  {
    this.toasterService = toasterService;
    this.updateRegion = fb.group({
      'regionName'     : ['',Validators.required],
      'regionEmail'    : ['',emailOrEmpty],
      'regionPhone'   : ['',[Validators.pattern('[0-9]*')]],
    });
  }

  ngOnInit()
  {
    let tkn = localStorage.getItem('ppsSuperAdminToken');
    let tknn = JSON.parse(tkn);
    this.regionDetail();
  }


  regionDetail()
  {
      this._sp.regionDetail(this.regionToEdit).subscribe(
        data => {
          this.regionDetails = data.data;

          this.regionDetailsLoading = false;
        },
        err => console.log(err)
     );
  }


  submitregionForm(value : any)
  {
    this.regionUpdateTrue = true;
    if( !this.updateRegion.valid )
    {
      return false;
    }

    let tkn = localStorage.getItem('ppsSuperAdminToken');
    let tknn = JSON.parse(tkn);
    value.id = this.regionDetails.id
    this._sp.updateRegion(value).subscribe(
      data => {
        if(data.success)
        {
          this.onSuccess.emit(data);
        }
      },
      err => console.log(err)
    );
    }

  }



