import { Injectable }              from '@angular/core';
import {HttpModule, Http,Response} from '@angular/http';
import { Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import * as myGlobals from '../shared/globals';

@Injectable()
export class LogStatusService {

  http: Http;
  returnCommentStatus:Object = [];
  token;
  constructor(public _http: Http)
  {
      this.http = _http;
      let tkn = localStorage.getItem('ppsSuperAdminToken');
      this.token = JSON.parse(tkn);
  }


  submitform(v)
  {
    let headers = new Headers();
    headers.append('Content-Type','application/x-www-form-urlencoded');
    return this.http.post('https://productprotectionsolutions.com/order/api/add-log-status',v, { headers }).map(
          (res: Response) => res.json() || {});
  }

  getStatus()
  {
    return this.http.get('https://productprotectionsolutions.com/order/api/log-status').map(
          (res: Response) => res.json() || {});
  }

  delete(value)
  {
    let headers = new Headers();
    headers.append('Content-Type','application/x-www-form-urlencoded');
    headers.append('ApiKey',this.token['apiKey']);
    return this.http.delete(myGlobals.baseUrl+'api/delete/id/'+value['id']+'/type/'+value['type'], { headers }).map(
          (res: Response) => res.json() || {});
  }


}



