import { Component, OnInit , AfterViewInit , ElementRef , ViewChild} from '@angular/core';
declare var jQuery: any;
import { SuperadminService }    from '../../superadmin.service';
import { ToasterModule, ToasterService , Toast} from 'angular2-toaster';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-list-region',
  templateUrl: './list-region.component.html',
  styleUrls: ['./list-region.component.css'],
  providers: [SuperadminService]
})
export class ListRegionComponent implements OnInit {
  private toasterService: ToasterService;
  regions = [];
  regionToEdit;
  regionEdit : boolean = false;
  perpage = 5;
  page = 1;
  region = '';
  Assignment : boolean =  false;
  totalItem;
  regionLoading : boolean = false;
  regionToDelete;
  searchText = '';
  regionListevent = 1;
  regionsBulk = [];

  constructor( private router: Router , private _sp: SuperadminService , toasterService: ToasterService)
  {
    this.toasterService = toasterService;
  }

  ngOnInit()
  {
    this.listregions(1);
  }

  ngAfterViewInit()
  {
    var thiss = this;
    jQuery('#updateModal').on('hidden.bs.modal', function () {
      thiss.regionEdit = false;
    });
    jQuery('#assignment').on('hidden.bs.modal', function () {
      thiss.Assignment = false;
    });
  }

  listregions(e)
  {
    this.regionListevent = e;
    this.regionLoading = true;
    var value = {};
    value['perpage'] = this.perpage;
    value['page'] = e;
    value['text'] = this.searchText;

      this._sp.listregions(value).subscribe(
        data => {
          var thiss = this;
           setTimeout(function(){
             thiss.regions = data.data.result;
             thiss.totalItem = data.data.total_rows;
             thiss.regionLoading = false;
           }, 500);
        },
        err => {
          if(err.status == 409)
          {
            this.regions = [];
            this.totalItem = 0;
            this.regionLoading = false;
          }
        }
     );
     return e;
  }

  editRegion(regionId)
  {
    console.log(regionId);
    this.regionToEdit = regionId;
    jQuery('#updateModal').modal('show');
    this.regionEdit = true;
  }

  handleRegionUpdate(event)
  {
    if(event.success)
    {
      this.regionToEdit = '';
      this.toasterService.pop('success', event.data ,'' );
      jQuery('#updateModal').modal('hide');
      this.listregions(1);
      this.regionEdit = false;
    }
  }

  updaetPerPage(a)
  {
    this.perpage = a;
    this.listregions(1);
  }

  deleteRegionConfirm(region)
  {
    this.regionToDelete = region;
    jQuery('#deleteModal').modal('show');
  }

  deleteRegion()
  {
    var value       = {}
    value['id']     = this.regionToDelete;
    value['type']   = 'region';
    this._sp.delete(value).subscribe(
      data => {
        if(data.success)
        {
          this.listregions(1);
          jQuery('#deleteModal').modal('hide');
          this.regionToDelete = '';
        }
      },
      err => console.log(err)
    );
  }

  deleteregion2()
  {
    var value       = {}
    value['id']     = this.regionToDelete;
    value['type']   = 'region';
    this._sp.delete(value).subscribe(
      data => {
        if(data.success)
        {
          this.listregions(1);
          jQuery('#deleteModal').modal('hide');
          this.regionToDelete = '';
        }
      },
      err => console.log(err)
    );
  }

  Search()
  {
    this.listregions(1);
  }
  assignStore(region)
  {
    this.region = region;
    this.Assignment = true;
    //console.log(this.region);
    jQuery('#assignment').modal('show');
  }
  deleteee()
  {
    var thiss = this;
    jQuery('input:checkbox.regionChecks').each(function () {
      if(this.checked)
      {
        var atr = jQuery(this).attr('dd');
        if(atr)
        thiss.regionsBulk.push(atr);
      }
      else
      {
        console.log('here');
        var atr = jQuery(this).attr('dd');
        if(atr)
        {
          var idx = thiss.regionsBulk.indexOf(atr);
          if(idx != -1)
          {
            thiss.regionsBulk.splice(idx,1);
          }
        }
      }

      });

      if(thiss.regionsBulk.length == 0)
      {
        thiss.toasterService.pop('error', 'Please select atleast 1 region' ,'' );
      }
      else
      {
        jQuery('#deleteModal2').modal('show');
      }
  }

  bulkDelete()
  {
    var thiss = this;
    jQuery('input:checkbox.regionChecks').each(function () {
      if(this.checked)
      {
        var atr = jQuery(this).attr('dd');
        if(atr)
        thiss.regionsBulk.push(atr);
      }
      else
      {
        console.log('here');
        var atr = jQuery(this).attr('dd');
        if(atr)
        {
          var idx = thiss.regionsBulk.indexOf(atr);
          if(idx != -1)
          {
            thiss.regionsBulk.splice(idx,1);
          }
        }
      }
      });

      if(thiss.regionsBulk.length == 0)
      {
        thiss.toasterService.pop('error', 'Please select atleast 1 region' ,'' );
      }

    var value       = {}
    var searchValue = this.regionsBulk.join('_');
    value['id']     = searchValue ;

    value['type']   = 'bulkregions';
    this._sp.delete(value).subscribe(
      data => {
        if(data.success)
        {
          this.listregions(1);
          jQuery('#deleteModal2').modal('hide');
          this.regionsBulk = [];
        }
      },
      err => console.log(err)
    );
  }

  isAllChecked()
  {
    return this.regions.every(_ => _.state);
  }
  checkAll(ev)
  {
    this.regions.forEach(x => x.email = ev.target.checked)
    // var objj = jQuery('.regionChecks');
    // console.log(objj);
  }


  // check(type)
  // {
  //   if(type == 'all')
  //   {
  //     for(let region of this.regions)
  //     {
  //       console.log(region['regionUserId'])
  //     }
  //   }
  // }

  }



