import { Component, OnInit } from '@angular/core';
import { FrontendService }    from '../../frontend/frontend.service';
declare var jQuery: any;
import { SuperadminService }    from '../superadmin.service';

@Component({
  selector: 'app-ins-sheets',
  templateUrl: './admin-ins-sheets.component.html',
  styleUrls: ['./admin-ins-sheets.component.css'],
  providers: [FrontendService,SuperadminService]
})

export class AdminInsSheetsComponent implements OnInit {

  sheets        = [];
  sheetToDelete = 0;

  constructor(private _front: FrontendService, private _sp:SuperadminService)
  {

  }

  ngOnInit()
  {
    this.getFaq();
  }

  jsonparse(val)
  {
    val = JSON.parse(val);
    return val;
  }

  getFaq()
  {
    this._front.getSettings().subscribe(
      data => {
        if(data.success)
        {
          for (let variable of data.data)
          {
            if(variable['setName'] == 'InstructionSheet')
            {
              variable['setValue'] = JSON.parse(variable['setValue']);
              this.sheets.push(variable);
            }
          }
        }
      },
      err => console.log(err)
    );
  }


  deleteSheet(pid)
  {
    this.sheetToDelete = pid;
    jQuery('#deleteModal').modal('show');
  }


  deleteSheetConfirm()
  {
    // console.log(this.sheets[this.sheetToDelete]['setId']);
    var value       = {};
    value['id']     = this.sheets[this.sheetToDelete]['setId'];
    value['type']   = 'inssheet';
    this._sp.delete(value).subscribe(
      data => {
        if(data.success)
        {
          this.sheets.splice(this.sheetToDelete,1);
          jQuery('#deleteModal').modal('hide');
          this.sheetToDelete = 0;
          this.getFaq();
        }
      },
      err => console.log(err)
    );
  }


}



