import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AbstractControl, FormArray, FormControl, FormBuilder, FormGroup , Validators} from '@angular/forms';
import {HttpModule, Http,Response} from '@angular/http';
import { ForgotPasswordService }    from '../forgot-password/forgot.service';
import {ToasterModule, ToasterService} from 'angular2-toaster';
declare var jQuery: any;

function passwordMatcher( c : AbstractControl )
{
  return c.get('password').value === c.get('passwordr').value
		? null : { 'passwordUnMatch' : true };
}


@Component({
  selector: 'app-reset-account-password',
  templateUrl: './reset-account-password.component.html',
  styleUrls: ['./reset-account-password.component.css'],
  providers: [ForgotPasswordService]
})


export class ResetAccountPasswordComponent implements OnInit {

  private toasterService: ToasterService;
  login : FormGroup;
  http: Http;
  submit : boolean = false;
  passwordStrength = '';
	passwordStrengthColor = '';
	passwordStrengthClass = '';
	passwordOk : boolean = false;
  userref;
  color = {}

  constructor(  route: ActivatedRoute , fb: FormBuilder , public _http: Http , private _forgot: ForgotPasswordService , private router: Router , toasterService: ToasterService)
  {
      this.toasterService = toasterService;
      this.http = _http;
      this.login = fb.group({
      'password' : ['',Validators.required],
      'passwordr' : ['',Validators.required]
    },{validator:passwordMatcher})


    let params: any = route.snapshot.params;
		this.userref = params.token;
  }


  ngOnInit()
  {

  }

  submitForm(v)
  {
    this.submit = true;
    if(!this.login.valid)
      return false;

        v['userref'] = this.userref;
      this._forgot.passwordChange(v).subscribe(
    data => {
        if(data.success)
        {
          this.toasterService.pop('success', data.success_msg , '' );
          this.login.reset();
          this.submit = false;
          setTimeout((router: Router) => {
              this.router.navigate(['/portal']);
          },1000);  //5s
        }
        else
        {
          this.toasterService.pop('error', data.error_msg , '' );
        }
    },
    err => console.log(err)
    );
  }

}



