import { OnInit, Component, Input, Output, EventEmitter } from '@angular/core';
import { FrontendService }    from '../frontend/frontend.service';
declare var jQuery: any;

@Component({
  selector: 'app-side-links',
  templateUrl: './side-links.component.html',
  styleUrls: ['./side-links.component.css'],
  providers: [FrontendService]
})

export class SideLinksComponent implements OnInit {
  settings = {};
  catalogue = '';
  
  constructor(private _front: FrontendService)
  {

  }

  ngOnInit() {
    this.getSettings()
  }

  getSettings()
  {
    this._front.getSettings().subscribe(
      data => {
        if(data.success)
        {
          for (let i = 0; i < data.data.length; i++)
          {
              this.settings[data.data[i]['setName']] = data.data[i]['setValue'];
          }
          console.log(this.settings['catalogue']);
          this.catalogue = this.settings['catalogue'];
        }
      },
      err => console.log(err)
   );
  }


}



