import { Component, OnInit } from '@angular/core';
import { ValidationErrors , AbstractControl , FormArray , FormControl , FormBuilder, FormGroup , Validators } from '@angular/forms';
declare var jQuery: any;
import { SuperadminService }    from '../../superadmin.service';
import { ToasterModule, ToasterService , Toast} from 'angular2-toaster';
import { Router, ActivatedRoute, Params } from '@angular/router';

function emailOrEmpty(control: AbstractControl): ValidationErrors | null {
    return control.value === '' ? null : Validators.email(control);
}
@Component({
  selector: 'app-add-region',
  templateUrl: './add-region.component.html',
  styleUrls: ['./add-region.component.css'],
  providers: [SuperadminService]
})
export class AddRegionComponent implements OnInit {
  private toasterService: ToasterService;
  regionAddTrue : boolean = false;
  addRegion : FormGroup;
  asignCrendials: boolean = false;
  regionAdding : boolean = false;
  constructor( private router: Router , private _sp: SuperadminService , toasterService: ToasterService , private fb: FormBuilder)
  {
    this.toasterService = toasterService;
    this.addRegion = fb.group({
      'regionName'     : ['',Validators.required],
      'regionEmail'    : ['',emailOrEmpty],
      'regionPhone'   : ['',[Validators.pattern('[0-9]*')]],
  });
  }

  ngOnInit()
  {
    let tkn = localStorage.getItem('ppsSuperAdminToken');
    let tknn = JSON.parse(tkn);
    console.log(tknn);
  }
  submitRegionForm(value : any)
  {
    console.log('in Contr');
    this.regionAddTrue = true;
    if( !this.addRegion.valid )
    {
      return false;
    }
    this.regionAdding = true;


    let tkn = localStorage.getItem('ppsSuperAdminToken');
    let tknn = JSON.parse(tkn);
    this._sp.addRegion(value).subscribe(
      data => {
        if(data.success)
        {
          this.regionAddTrue = false;
          this.addRegion.reset({
            regionName     : '',
            regionEmail    : '',
            regionPhone   : '',
          }
          );

          this.toasterService.pop('success', data.data ,'' );
          this.regionAdding = false;
        }
      },
      err => console.log(err)
   );
  }

  }



