import { Component, OnInit , AfterViewInit} from '@angular/core';
declare var jQuery: any;
import { SuperadminService }    from '../superadmin.service';
import { ToasterModule, ToasterService , Toast} from 'angular2-toaster';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-spnotification',
  templateUrl: './spnotification.component.html',
  styleUrls: ['./spnotification.component.css'],
  providers: [SuperadminService]
})
export class SpnotificationComponent implements OnInit {
  private toasterService: ToasterService;
  notifications = [];
  perpage = 5;
  page = 1;
  totalItem;
  notificationLoading : boolean = false;
  notificationToDelete;
  notificationListevent = 1;
  notificationsBulk = [];
  constructor( private router: Router , private _sp: SuperadminService , toasterService: ToasterService)
  {
    this.toasterService = toasterService;
  }

  ngOnInit()
  {
    this.listNotifications(1);
  }
  listNotifications(e)
  {
    this.notificationListevent = e;
    this.notificationLoading = true;
    var value = {};
    value['perpage'] = this.perpage;
    value['page'] = e;
      this._sp.listNotifications(value).subscribe(
        data => {
          var thiss = this;
           setTimeout(function(){
             thiss.notifications = data.data.result;
             thiss.totalItem = data.data.total_rows;
             thiss.notificationLoading = false;
           }, 500);
        },
        err => {
          if(err.status == 409)
          {
            this.notifications = [];
            this.totalItem = 0;
            this.notificationLoading = false;
          }
        }
     );
     return e;
  }
  updaetPerPage(a)
  {
    this.perpage = a;
    this.listNotifications(1);
  }
  deleteNotificationConfirm(store)
  {
    this.notificationToDelete = store;
    jQuery('#deleteModal').modal('show');
  }

  deleteNotification()
  {
    var value       = {}
    value['id']     = this.notificationToDelete;
    value['type']   = 'notification';
    this._sp.delete(value).subscribe(
      data => {
        if(data.success)
        {
          this.listNotifications(1);
          jQuery('#deleteModal').modal('hide');
          this.notificationToDelete = '';
        }
      },
      err => console.log(err)
    );
  }

  deleteNotification2()
  {
    var value       = {}
    value['id']     = this.notificationToDelete;
    value['type']   = 'notification';
    this._sp.delete(value).subscribe(
      data => {
        if(data.success)
        {
          this.listNotifications(1);
          jQuery('#deleteModal').modal('hide');
          this.notificationToDelete = '';
        }
      },
      err => console.log(err)
    );
  }
  deleteee()
  {
    var thiss = this;
    jQuery('input:checkbox.notificationChecks').each(function () {
      if(this.checked)
      {
        var atr = jQuery(this).attr('dd');
        if(atr)
        thiss.notificationsBulk.push(atr);
      }
      else
      {
        console.log('here');
        var atr = jQuery(this).attr('dd');
        if(atr)
        {
          var idx = thiss.notificationsBulk.indexOf(atr);
          if(idx != -1)
          {
            thiss.notificationsBulk.splice(idx,1);
          }
        }
      }

      });

      if(thiss.notificationsBulk.length == 0)
      {
        thiss.toasterService.pop('error', 'Please select atleast 1 notification' ,'' );
      }
      else
      {
        jQuery('#deleteModal2').modal('show');
      }
  }

  bulkDelete()
  {
    var thiss = this;
    jQuery('input:checkbox.notificationChecks').each(function () {
      if(this.checked)
      {
        var atr = jQuery(this).attr('dd');
        if(atr)
        thiss.notificationsBulk.push(atr);
      }
      else
      {
        console.log('here');
        var atr = jQuery(this).attr('dd');
        if(atr)
        {
          var idx = thiss.notificationsBulk.indexOf(atr);
          if(idx != -1)
          {
            thiss.notificationsBulk.splice(idx,1);
          }
        }
      }
      });

      if(thiss.notificationsBulk.length == 0)
      {
        thiss.toasterService.pop('error', 'Please select atleast 1 notification' ,'' );
      }

    var value       = {}
    var searchValue = this.notificationsBulk.join('_');
    value['id']     = searchValue ;

    value['type']   = 'bulkNotifications';
    this._sp.delete(value).subscribe(
      data => {
        if(data.success)
        {
          this.listNotifications(1);
          jQuery('#deleteModal2').modal('hide');
          this.notificationsBulk = [];
        }
      },
      err => console.log(err)
    );
  }

  isAllChecked()
  {
    return this.notifications.every(_ => _.state);
  }

  checkAll(ev)
  {
    this.notifications.forEach(x => x.state = ev.target.checked)
    // var objj = jQuery('.storeChecks');
    // console.log(objj);
  }
}



