import { Component, OnInit , Input , EventEmitter , Output , ElementRef , ViewChild} from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { FrontendService }    from '../../frontend/frontend.service';

@Component({
  selector: 'app-pheader',
  templateUrl: './pheader.component.html',
  styleUrls: ['./pheader.component.css'],
  providers: [FrontendService]
})

export class PheaderComponent implements OnInit {

  @Input() token: object;
  settings         = {}
  InstructionSheet = '';

  constructor(private _front : FrontendService , private router :  Router) { }

  ngOnInit()
  {
    this.getSettings();
  }

  logout()
  {
    localStorage.removeItem("ppsPortalToken");
  //  this.router.navigate(['/portal']);
  window.location.href = 'https://www.productprotectionsolutions.com/login.php';
  }

  getSettings()
  {
    this._front.getSettings().subscribe(
      data => {
        if(data.success)
        {
          for (let i = 0; i < data.data.length; i++)
          {
              this.settings[data.data[i]['setName']] = data.data[i]['setValue'];
          }
          console.log(this.settings['InstructionSheet']);
          this.InstructionSheet = this.settings['InstructionSheet'];
        }
      },
      err => console.log(err)
   );
  }

}



