import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { FormBuilder, FormGroup , Validators } from '@angular/forms';
import {HttpModule, Http,Response} from '@angular/http';
import {LogStatusService} from './log-status.service'
import {ToasterModule, ToasterService} from 'angular2-toaster';
declare var jQuery: any;


@Component({
  selector: 'app-log-status',
  templateUrl: './log-status.component.html',
  styleUrls: ['./log-status.component.css'],
  providers:[LogStatusService]
})


export class LogStatusComponent implements OnInit {
  login : FormGroup;
  http: Http;
  responseStatus:Object= [];
  arraydata=[]
  private toasterService: ToasterService;
  logd = '';

  constructor(toasterService: ToasterService, fb: FormBuilder , public _http: Http , private router: Router,private LogStatusService:LogStatusService) {
    this.toasterService = toasterService;
    this.http = _http;
    this.login = fb.group({
      'logText' : [null,Validators.required]
    })
   }

  ngOnInit()
  {
    this.getStatus();
  }

  getStatus()
  {
    this.LogStatusService.getStatus().subscribe(data=>{
      console.log(data);
      if(data.success)
      this.arraydata = data.data
    })
  }


submitform(value: any){
  this.LogStatusService.submitform(value).subscribe(data=>{
    if(data.success)
    {
      this.login.reset();
      this.getStatus();
      this.toasterService.pop('success', data.data + '' );
    }
  })
}


editPlan(id){

}

confirmationcheck(type,id)
{
  this.logd = id;
  if(type == 'confirm')
  {
    jQuery('#deleteModal2').modal('show');
    return false;
  }
    var value         = {};
    value['id']       = id;
    value['type']     = 'logStatus';
    this.LogStatusService.delete(value).subscribe(data=>{
    if(data.success)
    {
      this.toasterService.pop('success',data.data,'');
      jQuery('#deleteModal2').modal('hide');
      this.logd = '';
      this.getStatus();
    }
  })
}


}



