import { OnInit, Component, Input, Output, EventEmitter } from '@angular/core';
import { ViewEncapsulation } from '@angular/core';
import { FrontendService }    from '../frontend.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { FormArray , FormControl , FormBuilder, FormGroup , Validators } from '@angular/forms';
import {ToasterModule, ToasterService} from 'angular2-toaster';
declare var jQuery: any;
import * as myGlobals from '../../shared/globals';
import { PortalService }    from '../../portal/portal.service';


@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.css'],
  providers: [FrontendService,PortalService],
  // encapsulation: ViewEncapsulation.None,
})

export class ProductDetailsComponent implements OnInit {
  private toasterService: ToasterService;
  ProductDetails = {};
  ProductLoading : boolean = false;
  ProductId = '';
  page = 1;
  productImagePath = '';
  token;
  variationCount;
  ChoosedVariation = '';
  ChoosedVariationname = '';
  quantity:number = 1;
  LoggedIn : boolean = false;
  ChoosedVariationQuantity = 0;
  Stores = [];
  images = [];
  imgPath = '';
  whichStore = '';
  NoStore = false;
  NotavailableforStore = true;
  NotavailableError = false;

  constructor(private _service: PortalService, private activatedRoute: ActivatedRoute  , public fb: FormBuilder , private router: Router , private _front: FrontendService , toasterService: ToasterService)
  {
    let params: any = this.activatedRoute.snapshot.params;
		this.ProductId = params.ProductId;
		this.page = params.page;
    localStorage.setItem("ppsProductPage", JSON.stringify({ page : this.page}) );
    this.toasterService = toasterService;
  }

  ngOnInit()
  {
    if( this.ProductId != '')
    this.getProductDetails(this.ProductId);
    if (localStorage.getItem("ppsPortalToken") !== null)
    {
      let tkn = localStorage.getItem('ppsPortalToken');
      this.token = JSON.parse(tkn);
      this.LoggedIn = true;
    }
    if(this.LoggedIn && (this.token.userType == 3 || this.token.userType == 9))
    {
      // console.log('this.token.apdm.apdmID');
      // console.log(this.token.apdm.apdmUserId);
      this.getStores(this.token.apdm.apdmID,this.token['userType']);
    }
    var whichStore  = localStorage.getItem('whichStore');
    if(whichStore)
    {
      this.whichStore = whichStore;
    }
  }

  strChng()
  {
    localStorage.setItem('whichStore',this.whichStore);
  }

  getProductDetails(id)
  {
    this._front.productDetails(id).subscribe(
      data => {
        if(data.data['images'])
        this.images = JSON.parse(data.data['images']);
        this.ProductDetails = data.data;
        if(data.data.productVariations)
        {
          this.variationCount = data.data.productVariations.length;
          if(this.variationCount != 0)
          {
            this.ProductDetails['productPrice'] = data.data.productVariations[0].productVarPrice;
            this.ChoosedVariation         = data.data.productVariations[0].productVarID;
            this.ChoosedVariationname     = data.data.productVariations[0].productVarItemId
            this.ChoosedVariationQuantity = data.data.productVariations[0].productVarItemQuantity
          }
        }
        if(data.data.productImage !='')
        this.productImagePath = myGlobals.baseUrl + 'api/assets/uploads/catPics/' + data.data.productImage;
        else
        this.productImagePath = 'assets/img/demo.png';
      },
      err => console.log(err)
   );
  }

  addToCart(p)
  {
   //  if(this.token.userType == 3 || this.token.userType == 9)
   //  {
   //    if (this.whichStore == '')
   //    {
   //      this.NoStore = true;
   //      return false;
   //    }
   // }

   // if(this.token.userType == 3 || this.token.userType == 9)
   // {
    // for (let i in this.Stores)
    // {
    //   if(this.Stores[i]['storeUserId'] == this.whichStore)
    //   {
    //     var classs = this.Stores[i]['storeClass'];
    //   }
    // }

   //  for(let ii in this.ProductDetails['classes'])
   //  {
   //    if(this.ProductDetails['classes'][ii]['productClass'] == classs)
   //    this.NotavailableforStore = false;
   //  }
   //
   //  if(this.NotavailableforStore)
   //  {
   //    this.NotavailableError = true;
   //    return false;
   //  }
   //  else
   //  this.NotavailableError = false;
   // }
    this.NotavailableError = false;
    var v = {};
    v['userId'] = this.token['userId'];
    v['productId'] = this.ProductId;
    v['quantity'] = this.quantity;
    if(this.ChoosedVariation !='')
    v['variationid'] = this.ChoosedVariation;
    else
    v['variationid'] = '';

    v['userType'] = this.token['userType'];

    this._front.addToCart(v).subscribe(
      data => {
        if(data.success)
        {
          this.toasterService.pop('success',data.data,'');
        }
      },
      err => console.log(err)
   );
  }

  choooseVariation(varID,VarName,i)
  {
    this.ChoosedVariationQuantity = this.ProductDetails['productVariations'][i]['productVarItemQuantity'];
    this.ProductDetails['productPrice'] = this.ProductDetails['productVariations'][i]['productVarPrice'];
    this.ChoosedVariation = varID;
    this.ChoosedVariationname = VarName;
  }

  logout()
  {
    localStorage.removeItem("ppsPortalToken");
    this.router.navigate(['/customer-login']);
  }

  showimg(imgPath)
  {
    this.imgPath = imgPath;
    jQuery('#img').modal('show');
  }

  getStores(apdm,type)
  {
    this._service.getAdpmStores(apdm,type).subscribe(
      data => {
        if(data.status == 200)
        {
          this.Stores = data.data;
        }
       }
    );
  }

}



