import { Component, OnInit , AfterViewInit , ElementRef , ViewChild} from '@angular/core';
declare var jQuery: any;
import { SuperadminService }    from '../superadmin.service';
import { ToasterModule, ToasterService , Toast} from 'angular2-toaster';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AbstractControl, FormArray, FormControl, FormBuilder, FormGroup , Validators , ValidationErrors} from '@angular/forms';

@Component({
  selector: 'app-faq-update',
  templateUrl: './faq-update.component.html',
  styleUrls: ['./faq-update.component.css'],
  providers: [SuperadminService]
})

export class FaqUpdateComponent implements OnInit {
  faq = '';
  private toasterService: ToasterService;

  constructor( public fb: FormBuilder , private router: Router , private _sp: SuperadminService , toasterService: ToasterService)
  {
    this.toasterService = toasterService;
  }

  ngOnInit()
  {
    this.getFaq();
  }

  getFaq()
  {
    this._sp.getWhere('faq',4).subscribe(
      data => {
        if(data.success)
        {
          this.faq = data.data['setValue'];
        }
      },
      err => console.log(err)
    );
  }

  faqUpdate()
  {
    var value               = {};
    value['type']          = 'faq';
    value['setValue']     = this.faq;
    this._sp.update(value).subscribe(
      data => {
        if(data.success)
        {
          this.toasterService.pop('success',data.data,'');
        }
      },
      err => console.log(err)
    );
  }

}



