import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { FormBuilder, FormGroup , Validators } from '@angular/forms';
import { ForgotPasswordService }    from './forgot.service';
import {ToasterModule, ToasterService} from 'angular2-toaster';
import {HttpModule, Http,Response} from '@angular/http';
@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css'],
  providers: [ForgotPasswordService]
})
export class ForgotPasswordComponent implements OnInit {
forgot 	: FormGroup;
submitted = false;
private toasterService: ToasterService;
http: Http;
submitAttempt : boolean = false;
color = {}

  constructor( public fb: FormBuilder , public _http: Http, private router: Router , toasterService: ToasterService,private _forgotservice:ForgotPasswordService ) {
          this.toasterService = toasterService;
          this.http = _http;
      		this.forgot = this.fb.group({
      			'com_email' 	: ['',[Validators.required, Validators.email]]
      		});
        }

  ngOnInit() {
  }
  responseStatus:Object= [];

forgotpassword(value: any,forgot)
{

  this._forgotservice.forgot(value).subscribe(
    data => {
      console.log(data);
      if(data.status == 200)
      {
          this.toasterService.pop('success', 'Password reset link sent on your email' + '' );
      }
    },
  //   err => {
  //     this.responseStatus = {'error':'Unknown Error occured , try again later'}
  // }
 );
 this.forgot.reset()
}

hidemsg(event: any)
{
  this.responseStatus = [];
}
}



