import { Component, OnInit , Input , EventEmitter , Output , ElementRef , ViewChild} from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { FormBuilder, FormGroup , Validators } from '@angular/forms';
import {HttpModule, Http,Response} from '@angular/http';
import { PortalService }    from '../portal.service';
declare var jQuery: any;


@Component({
  selector: 'app-psidebar',
  templateUrl: './psidebar.component.html',
  styleUrls: ['./psidebar.component.css'],
  providers: [PortalService]
})
export class PsidebarComponent implements OnInit {
  @Input() token: object;
  settings         = {}
  InstructionSheet = '';
  catalogue = '';
  constructor(private _service: PortalService, private router: Router) { }

  ngOnInit() {
    this.getSettings();
  }

  ngAfterViewInit ()
  {
    console.log(jQuery);
    jQuery('[data-play="dropdown"]').click(function(){
      jQuery(this).parent('.nav-dropdown').toggleClass('open');
    })
  }
  getSettings()
  {
    this._service.getSettings().subscribe(
      data => {
        if(data.success)
        {
          for (let i = 0; i < data.data.length; i++)
          {
              this.settings[data.data[i]['setName']] = data.data[i]['setValue'];
          }
          console.log(this.settings['InstructionSheet']);
          this.InstructionSheet = this.settings['InstructionSheet'];
          this.catalogue = this.settings['catalogue'];
        }
      },
      err => console.log(err)
   );
  }

}



