import { Component, OnInit , AfterViewInit} from '@angular/core';
declare var jQuery: any;
import { PortalService }    from '../portal.service';
import { ngxCsv } from 'ngx-csv/ngx-csv';
import { ToasterModule, ToasterService , Toast} from 'angular2-toaster';
import { Router, ActivatedRoute, Params } from '@angular/router';
@Component({
  selector: 'app-pexport-report',
  templateUrl: './pexport-report.component.html',
  styleUrls: ['./pexport-report.component.css'],
  providers: [PortalService]
})
export class PexportReportComponent implements OnInit {
  token = {};
  stores = [];
  model:any = {};
  apls = [];
  storeLoading : boolean = false;
  isSubmit : boolean = false;
  fromDateError : boolean = false;
  toDateError : boolean = false;

  private toasterService: ToasterService;
  constructor( private router: Router , private _service: PortalService , toasterService: ToasterService)
  {
    this.model.from_date = '';
    this.model.to_date = '';
    this.model.apl = '';
    this.model.store_id = '';
    this.toasterService = toasterService;
    var tokenn  = localStorage.getItem("ppsPortalToken");
    this.token   = JSON.parse(tokenn);
  }
  export()
  {
    this.isSubmit = true;
    if(this.model.from_date == '' && this.model.to_date != '')
    {
      this.toasterService.pop('error','Please select from date','');
      return false;
    }
    else if(this.model.from_date != '' && this.model.to_date == '')
    {
      this.toasterService.pop('error','Please select to date','');
      return false;
    }
    else
    {
      console.log(this.model);
      this._service.exportReports(this.model).subscribe(
        data => {
        //  console.log(data.data);return false;

            // var orders = [];
            // for (let i = 0; i < data.data.length; i++)
            // {
            //   var order = {};
            //   order['category'] = data.data[i].catName;
            //   //order['products'] = data.data[i].catName;
            //   for(let j = 0; j < data.data[i].products; i++)
            //   {
            //     var product = {};
            //     product['product_name'] = data.data[i].products[j].productCode+'-'+data.data[i].products[j].productName;
            //     product['product_qty'] = data.data[i].products[j].qty;
            //     product['product_price'] = data.data[i].products[j].price;
            //     product['apdl'] = data.data[i].products[j].apdmFirstName+' '+data.data[i].products[j].apdmLastName;
            //     order['products'] = product;
            //   }
            //   order['total_qty'] = data.data[i].total_qty;
            //   order['total_amount'] = data.data[i].total_amount;
            //   orders.push(order);
            // }
                //console.log(data.data);
            var hds = ['Order Number','Apdl','Store','Product', 'Quantity', 'Amount'];



            var options = {
              fieldSeparator: ',',
              quoteStrings: '"',
              decimalseparator: '.',
              headers: hds,
              showTitle: false,
              useBom: true,
              removeNewLines: false,
            };
            var name = 'PPS_Order_reports';

            new ngxCsv(data.data, name ,options);
        },
        err => {
          if(err.status == 409)
          {
            this.stores = [];
            this.apls = [];
            this.storeLoading = false;
          }
        }
     );
    }
  }
  ngOnInit()
  {   var tokenn  = localStorage.getItem("ppsPortalToken");
      this.token   = JSON.parse(tokenn);
    this.getData();
  }
  getData()
  {
      this.storeLoading = true;
      this._service.getexportReportData().subscribe(
        data => {
          console.log(data)

             this.stores = data.stores;
             this.apls = data.apls;

        },
        err => {
          if(err.status == 409)
          {
            this.stores = [];
            this.apls = [];
            this.storeLoading = false;
          }
        }
     );
  }
  }



